<template>
  <!--Section Start-->
  <div class="section section-padding bg-gray">
    <div class="container">
      <div class="section-title centered">
        <span class="subtitle">{{ serviceData.services_subtitle }}</span>
        <h3 class="title">{{ serviceData.services_title }}</h3>
      </div>
      <div class="row">
        <div class="col-lg-4 col-md-6" v-for="item in serviceData.main_service" :key="item.id">
          <div class="sigma_service style-6">
            <div class="sigma_service-thumb">
              <img :src="getItem(item.img)" alt="img" />
              <i class="flaticon-truck icon-box"></i>
            </div>
            <div class="sigma_service-body">
              <h5>
                <router-link to="/product">{{ item.title }}</router-link>
              </h5>
              <p>
                {{ item.desc }}
              </p>
              <router-link to="/product" class="btn-link secondary-color">
                Read More
                <i class="far fa-angle-right"></i>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!--Section End-->
</template>

<script>
import data from '../Data/data.json'
export default {
    name: "services",
    data(){
        return{
          serviceData: ''
        }
    },
    mounted(){
      this.serviceData = data.services
    },
    methods: {
      getItem(pic){
        if(pic){
          return require('../assets/img/ourservices/' + pic)
        } else{
          return null
        }
      }
    }
}
</script>